
// regular
@font-face {
    font-family: 'proxima_nova_rgregular';
    src: url('./proxima-nova/ProximaNova-Reg-webfont.eot');
    src: url('./proxima-nova/ProximaNova-Reg-webfont.eot?#iefix')
            format('embedded-opentype'),
        url('./proxima-nova/ProximaNova-Reg-webfont.woff') format('woff'),
        url('./proxima-nova/ProximaNova-Reg-webfont.ttf')
            format('truetype'),
        url('./proxima-nova/ProximaNova-Reg-webfont.svg#proxima_nova_rgregular')
            format('svg');
    font-weight: normal;
    font-style: normal;
}

